/* eslint-disable camelcase */
import React, { FC, ReactNode } from 'react'
import { Typography } from '@material-ui/core'
import { FieldError } from 'react-hook-form'
import useStyles from './FormContainer.styles'

interface IFormContainerProps {
  title: string
  subtitle?: string
  description: string
  error: { [fieldName: string]: FieldError }
  noTextContainer?: boolean
  children: ReactNode
}

const FormContainer: FC<IFormContainerProps> = ({ children, title, subtitle, description, error, noTextContainer }) => {
  const classes = useStyles()
  return (
    <div className={classes.root}>
      <Typography className={classes.title} color="textPrimary" variant="h2" component="h1">
        {title}
      </Typography>
      {!noTextContainer && (
        <div className={classes.textContainer}>
          <Typography className={classes.subtitle} variant="body1">
            {subtitle}
          </Typography>
          {description && (
            <Typography
              className={
                error?.non_field_errors
                  ? `${classes.description} ${classes.description_color_red}`
                  : classes.description
              }
              color="textSecondary"
              variant="body1"
            >
              {/* eslint-disable-next-line no-nested-ternary */}
              {error?.non_field_errors ? error?.non_field_errors?.message : description}
            </Typography>
          )}
        </div>
      )}
      {children}
    </div>
  )
}

export default React.memo(FormContainer)
