import React, { useEffect, FC } from 'react'
import { MoonLoader } from 'react-spinners'
import { Button, TextField, Link, Typography, Container } from '@material-ui/core'
import { Link as RouterLink, Navigate } from 'react-router-dom'
import * as yup from 'yup'
import { Controller, useForm, SubmitHandler } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import useStyles from './LoginPage.style'
import FormContainer from '../../../components/Sub/FormContainer/FormContainer'
import {
  PROJECT_NAME,
  LOG_PAGE_DESCRIPTION,
  LOG_FORM_EMPTY_PASSWORD_ERROR,
  LOG_FORM_EMPTY_EMAIL_ERROR,
  LOG_FORM_BAD_EMAIL_ERROR,
  LOG_FORM_EMAIL_PLACEHOLDER,
  LOG_FORM_PASS_PLACEHOLDER,
} from '../../../utils/constants'
import VacationMessage from '../../../components/Main/LoginPage/VacationMessage/VacationMessage'
import InformationWindow from '../../../components/Main/LoginPage/InformationWindow/InformationWindow'
import { formatDayMonthYearWithComma } from '../../../utils/dates'
import processAuthServerError from '../../../utils/auth-server-error'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { signIn, getProfile } from '../../../services/axios/auth'
import { clearRequestProfile, setUnlogged } from '../../../services/slices/auth-slice'

import { isConsultant, isUser, isVacancyManager } from '../../../utils/check-auth'
import { TSignInData } from '../../../services/types/auth/auth-types'

const schema = yup.object().shape({
  email: yup.string().trim().required(LOG_FORM_EMPTY_EMAIL_ERROR).email(LOG_FORM_BAD_EMAIL_ERROR),
  password: yup.string().required(LOG_FORM_EMPTY_PASSWORD_ERROR),
})

const LoginPage: FC = () => {
  const dispatch = useAppDispatch()
  const { isLoggedIn, role, profile, signInRequest, getProfileRequest } = useAppSelector((store) => store.auth)

  const [onVacation, setOnVacation] = React.useState(false)
  const [vacationEndDate, setVacationEndDate] = React.useState('')

  const isLoading = signInRequest.status === 'pending'

  const {
    control,
    handleSubmit,
    formState: { errors },
    setError,
  } = useForm<TSignInData>({
    resolver: yupResolver<yup.AnyObjectSchema>(schema),
    mode: 'onChange',
  })

  const handeBackClick = () => {
    setOnVacation(false)
    setVacationEndDate('')
    dispatch(clearRequestProfile())
  }

  useEffect(() => {
    if (signInRequest.isError) processAuthServerError(signInRequest.errorInfo, setError)
  }, [signInRequest.isError])

  useEffect(() => {
    if (getProfileRequest.errorInfo?.end_date) {
      setVacationEndDate(formatDayMonthYearWithComma(+getProfileRequest.errorInfo.end_date))
      setOnVacation(true)
    }
  }, [getProfileRequest.isError])

  const onSubmit: SubmitHandler<TSignInData> = async (data) => {
    dispatch(setUnlogged())
    let userRole
    try {
      const result = await dispatch(signIn(data)).unwrap()
      userRole = result?.role.toLowerCase()
    } catch {
      return undefined
    }
    try {
      if (userRole && isUser(userRole)) {
        await dispatch(getProfile())
      }
    } catch {
      return undefined
    }
    return undefined
  }

  const classes = useStyles()

  if (isLoggedIn && !!profile) return <Navigate to={{ pathname: '/' }} />
  if (isLoggedIn && isUser(role) && profile === null) return <Navigate to={{ pathname: '/onboarding/personal-info' }} />
  if (isLoggedIn && isConsultant(role)) return <Navigate to={{ pathname: '/onboarding/personal-info' }} />
  if (isLoggedIn && isVacancyManager(role)) return <Navigate to={{ pathname: '/vacancies/list' }} />

  return (
    <Container className={classes.root}>
      {onVacation ? (
        <InformationWindow handleBackClick={handeBackClick}>
          <VacationMessage daysBackWork={vacationEndDate} />
        </InformationWindow>
      ) : (
        <>
          <FormContainer title={PROJECT_NAME} subtitle="" description={LOG_PAGE_DESCRIPTION} error={errors}>
            <form onSubmit={handleSubmit(onSubmit)}>
              <fieldset className={classes.fieldset}>
                <Controller
                  name="email"
                  control={control}
                  render={({ field }) => (
                    <TextField
                      {...field}
                      error={!!errors.email?.message}
                      helperText={errors.email?.message || ' '}
                      className={`${classes.input} ${classes.firstInput}`}
                      variant="outlined"
                      placeholder={LOG_FORM_EMAIL_PLACEHOLDER}
                      inputProps={{ disableUnderline: true }}
                    />
                  )}
                />
                <Controller
                  name="password"
                  control={control}
                  render={({ field }) => (
                    <TextField
                      {...field}
                      type="password"
                      error={!!errors.password?.message}
                      helperText={errors.password?.message || ' '}
                      className={classes.input}
                      variant="outlined"
                      placeholder={LOG_FORM_PASS_PLACEHOLDER}
                      inputProps={{ disableUnderline: true }}
                    />
                  )}
                />
                <Typography className={classes.title} color="textPrimary" variant="body2">
                  <Link
                    className={classes.link}
                    component={RouterLink}
                    to="/reset-password"
                    color="primary"
                    underline="none"
                  >
                    Не помню пароль
                  </Link>
                </Typography>
                {isLoading ? (
                  <MoonLoader color="#1d6bf3" loading size={39} />
                ) : (
                  <Button className={classes.button} type="submit">
                    Войти
                  </Button>
                )}
              </fieldset>
            </form>
          </FormContainer>
          <Typography className={classes.registerTitle} variant="body1">
            Новый пользователь?{' '}
            <Link className={classes.registerLink} component={RouterLink} to="/sign-up" underline="none">
              Зарегистрироваться
            </Link>
          </Typography>
        </>
      )}
    </Container>
  )
}

export default React.memo(LoginPage)
