import {
  AUTH_ACCOUNT_NOT_FOUND_ERROR,
  FORGOT_PASSWORD_ERROR,
  FORGOT_PASSWORD_ERROR_FORM_TEXT,
  LOGIN_ENTER_ERROR,
  NOT_EQUAL_PASSWORD_ERROR,
  PASSWORD_LIKE_EMAIL_ERROR,
  PASSWORD_LIKE_EMAIL_ERROR_FORM_TEXT,
  REG_FORM_BAD_EMAIL_ERROR,
  REG_FORM_CONFIRM_PASS_ERROR,
  REG_FORM_MINMAX_SYMBOLS_ERROR,
  REGISTER_INVALID_MAIL,
  REGISTER_PRIVATE_INFO_ERROR,
  REGISTER_SHORT_PASSWORD_ERROR,
  REGISTER_UNIQUE_MAIL,
  REGISTER_UNIQUE_MAIL_FORM_TEXT,
  REGISTER_WEAK_NUMBER_PASSWORD_ERROR,
  REGISTER_WEAK_PASSWORD_ERROR,
} from './constants'

const authErrorChecker = (error, field, setError) => {
  switch (error) {
    case REGISTER_WEAK_NUMBER_PASSWORD_ERROR: {
      setError(field, {
        type: 'server',
        message: REGISTER_WEAK_NUMBER_PASSWORD_ERROR,
      })
      break
    }
    case REGISTER_WEAK_PASSWORD_ERROR: {
      setError(field, {
        type: 'server',
        message: REGISTER_WEAK_PASSWORD_ERROR,
      })
      break
    }
    case REGISTER_SHORT_PASSWORD_ERROR: {
      setError(field, {
        type: 'server',
        message: REG_FORM_MINMAX_SYMBOLS_ERROR,
      })
      break
    }
    case REGISTER_PRIVATE_INFO_ERROR: {
      setError(field, {
        type: 'server',
        message: REGISTER_PRIVATE_INFO_ERROR,
      })
      break
    }
    case REGISTER_UNIQUE_MAIL: {
      setError(field, {
        type: 'server',
        message: REGISTER_UNIQUE_MAIL_FORM_TEXT,
      })
      break
    }
    case REGISTER_INVALID_MAIL: {
      setError(field, {
        type: 'server',
        message: REG_FORM_BAD_EMAIL_ERROR,
      })
      break
    }
    case AUTH_ACCOUNT_NOT_FOUND_ERROR: {
      setError('non_field_errors', {
        type: 'server',
        message: LOGIN_ENTER_ERROR,
      })
      break
    }
    case NOT_EQUAL_PASSWORD_ERROR: {
      setError('non_field_errors', {
        type: 'server',
        message: REG_FORM_CONFIRM_PASS_ERROR,
      })
      break
    }
    case FORGOT_PASSWORD_ERROR: {
      setError(field, {
        type: 'server',
        message: FORGOT_PASSWORD_ERROR_FORM_TEXT,
      })
      break
    }
    case PASSWORD_LIKE_EMAIL_ERROR: {
      setError(field, {
        type: 'server',
        message: PASSWORD_LIKE_EMAIL_ERROR_FORM_TEXT,
      })
      break
    }
    default: {
      setError('non_field_errors', {
        type: 'server',
        message: 'Серверная ошибка',
      })
    }
  }
}

const processAuthServerError = (serverErrors, setError) => {
  if (typeof serverErrors !== 'object') {
    setError('non_field_errors', {
      type: 'server',
      message: 'Серверная ошибка',
    })
  } else {
    Object.keys(serverErrors).forEach((field) => {
      // Ошибки могут возвращаться массивом, учитываем это
      if (Array.isArray(serverErrors[field])) {
        // Достаточно вывести первую ошибку
        authErrorChecker(serverErrors[field][0].toString(), field, setError)
      } else {
        authErrorChecker(serverErrors[field].toString(), field, setError)
      }
    })
  }
}

export default processAuthServerError
