import { format, formatDuration, intervalToDuration } from 'date-fns'
import { ru } from 'date-fns/locale'

export function formatDayMonth(value) {
  return value ? format(new Date(value), 'dd MMM', { locale: ru }).slice(0, -1) : ''
}

export function formatDayMonthYear(value) {
  return format(new Date(value), 'dd MMM yyyy', { locale: ru }).split('.').join('')
}

export function formatDayFullMonthYear(value) {
  return format(new Date(value), 'dd MMMM yyyy', { locale: ru }).split('.').join('')
}

export function formatDayMonthYearWithComma(value) {
  return format(new Date(value), 'dd MMM yyyy', { locale: ru }).split('.').join(',')
}

export function formatDayFullMonth(value) {
  return value ? format(new Date(value), 'dd MMMM', { locale: ru }) : ''
}

export function formatRusStyle(value) {
  return value ? format(new Date(value), 'dd.MM.yyyy') : ''
}

export function formatApiDate(value) {
  return format(value, "yyyy-MM-dd'T'HH:mm:ss.SSSxxx")
}

export function formatDbDate(value) {
  return format(value, 'yyyy-MM-dd')
}

export function getDurationInYearWeekDay(value) {
  if (value) {
    // value * 86400000 - переводим дни в миллисекунды
    const duration = intervalToDuration({ start: Date.now() - value * 86400000, end: Date.now() })
    return `${formatDuration(duration, { format: ['years', 'months', 'days'], locale: ru })}`
  }
  return ''
}

export default formatDayMonth
